@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;400&display=swap);
:root {
  --color-background: #fcffed;
  --color-secundary-lighter: #f9a826;
  --color-secundary-dark: #f48400;
  --color-primary-lighter: #6aa8ca;
  --color-primary-dark: #0089ef;
  --color-line-in-grey: #c4c4c4;
}

* {
  margin: 0;
  padding: 0;
  outline: 0;
  box-sizing: border-box;
}

html, body, #root {
  min-height: 100%;
}

body {
  background-color: #fcffed;
  background-color: var(--color-background);
  -webkit-font-smoothing: antialiased;
  height: 100vh;
}

body, input, button {
  font-family: 'Roboto', Arial, Helvetica, sans-serif;
}

button {
  cursor: pointer;
 
 
}

.yellow-button {
  background-color: #f9a826;
  background-color: var(--color-secundary-lighter);
  color: #FFFFFF;
  border: none;
  transition: 0.2s ease-in-out;
}

.yellow-button:hover {
  background-color: #f48400;
  background-color: var(--color-secundary-dark);
}


.container {
    width: 100vw;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

.content {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.content img {
    width: 120px;
    margin-bottom: 20px;
}
.content h1 {
    margin-bottom: 25px;
    color: var(--color-secundary-lighter);
    font-size: 20px;
}

.content h2 {
    margin-bottom: 30px;
    color: var(--color-primary-dark);
    font-size: 30px;
}

.content input {
    width: 100%;
    height: 40px;
    margin-bottom: 10px;
    text-align: center;
    font-size: 18px;
}

.content button {
    width: 100%;
    height: 40px;
    border-radius: 4px;
    font-size: 18px;
}

.table {
    width: 100vw;
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 40px;
    
}

.table h3 {
    margin-top: 80px;
    margin-bottom: 80px;
    font-size: 35px;
    color: var(--color-primary-dark);
    
}

.table table {
    width: 90vw;
    border: 1px solid #f9a826;
    border-radius: 6px;
    
  
}

.table table tr {
    height: 40px;
    
}

.table table th {
    width: 20%;
    border: 1px solid #f9a826;
    border-radius: 4px;
    background-color: var(--color-secundary-lighter);
    color: black;
    font-size: larger;
  
}
.table table td {
    text-align: center;
    border: 0.5px solid cadetblue;
    border-radius: 4px;
    word-break: break-all;
   
}

td button img{
    width: 15px;
    margin-right: 20px;
    text-align: center;
    
}

#new-post {
    margin-top: 10vh;
    width: 120px;
    height: 32px;
    font-size: 18px;
    border-radius: 8px;       
}

#sair{
    margin-top: -3.5vh;
    margin-right: -50vh;
    width: 120px;
    height: 32px;
    font-size: 18px;
    border-radius: 8px;
}
#trash{
    margin-right: -20px;
    
}
#edit{
    margin-right: -20px;
    
}

td button {
    margin-right: 40px;
    background: none;
    border: none;
}

#create-modal {
    width: 90%;
    height: 100%;
    display: flex;
    align-items: center;
    flex-direction: column;
}

#create-modal form {
    width: 35vw;
    display: flex;
    flex-direction: column;
}

#create-modal form button {
    font-size: 18px;
    width: 100px;
    border-radius: 8px;
    height: 32px;
    margin: 10px 0 10px;
    margin-right: 10px;
    
}


#create-modal form label {
    margin-top: 30px;
}

.close-button {
    margin-top: 30px;
    margin-left: 100px;
    
    
}
 







